import {React, useState, useEffect} from 'react'
import {CDataTable, CButton, CInput, CBadge} from '@coreui/react'
import ModalKomoditas from './modalKomoditas'
import { useHistory } from 'react-router-dom'
import supabase from '../../../../supabase'
import { useDispatch } from 'react-redux'

const Viavalen = () =>{
    const dispatch = useDispatch()
    const history = useHistory()
    const [modal, setModal] = useState(false)
    const [item, setItem] = useState([])
    useEffect(async() => {
        let { data: td_data, error } = await supabase
        .from('td_data')
        .select('*')  
        setItem(td_data) 
        console.log(item) 
      },[]);    
    const handleEdit = async (x) =>{
        let arr = []
        arr.push(x)
        let objKomoditas = {}
        objKomoditas.kode = x.kdKomoditas
        objKomoditas.nama = x.urKomoditas
        let objJnsData = {}
        objJnsData.kode = x.kdJenisData
        objJnsData.nama = x.urJnsData
        dispatch({type: 'set', ajubaru: arr })
        dispatch({type: 'set', komoditas: objKomoditas })
        dispatch({type: 'set', jnsdata: objJnsData })
        history.push('/pasokankebutuhan/new')          
    }
    return(
        <>
        <ModalKomoditas modal={modal} callback={()=>setModal(false)}/>
        <div className="card px-3 py-3" style={{width:'100%', borderRadius:'18px',boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
            <div className="d-flex flex-row justify-content-center align-items-center" style={{height:'100px'}}>
                <h4>Riwayat Data Kebutuhan dan Pasokan</h4>
            </div>
            <div className="d-flex flex-row justify-content-end mr-1 mb-2">
                <div className="mr-auto">
                    <CButton style={{borderRadius:'12px', backgroundColor:'#606365', color:'white'}} onClick={()=>history.push('/')} className="btn"><i class="fa fa-chevron-left"></i> Kembali</CButton>                    
                </div>
                <div className="mr-3">
                    <CInput style={{borderRadius:'12px', width:'20vw', backgroundColor:'#fafafc'}} placeholder="Search"/>
                </div>
                <div>
                    <CButton style={{borderRadius:'12px', backgroundColor:'#20c997', color:'white'}} onClick={()=>setModal(true)} className="btn"><i class="fas fa-plus-square"></i> Tambah</CButton>                    
                </div>
            </div>
            <div className="mt-2 mx-3">
                <CDataTable
                responsive
                items={item}
                // addTableClasses="josss"
                fields={[
                    {key:'nomor', label:'No', _style: { width: '5%'}},
                    {key:'noaju', label:'Nomor AJU'},
                    {key:'komoditas', label:'Komoditas'},
                    {key:'jnsdata', label:'Jenis Data'},                    
                    {key:'status', label:'Status'},
                    {key:'action', label:'Action'},
                ]}
                scopedSlots={{
                    'jnsdata':
                    (item, index)=>{
                        let jnsdata
                        switch(item.kdJenisData) {
                        case "01":
                            jnsdata = "Pasokan"
                            break;
                        case "02":
                            jnsdata = "Kebutuhan"
                            break;
                        }                         
                        return(
                            <td>
                                {jnsdata}
                            </td>
                        )
                    },
                    'action':
                    (item, index)=>{
                        return(
                            <td>
                                <CButton onClick={()=>handleEdit(item)} style={{borderRadius:'12px', backgroundColor:'#c8ced3', fontSize:'.76562rem'}} className="btn btn-sm"><i class="fa fa-chevron-right"></i></CButton>
                            </td>
                        )
                    },
                    'status':
                    (item, index)=>{
                        let status
                        let color
                        switch(item.kd_status) {
                        case "01":
                            status = "Draft"
                            color = "success"
                            break;
                        case "02":
                            status = "Terkirim"
                            color = "info"
                            break;
                        }                         
                        return(
                            <td>
                                <CBadge color={color}>{status}</CBadge>
                            </td>
                        )
                    },
                    'nomor':
                    (item,index)=>{
                        return(
                            <td>
                                {index+1}
                            </td>
                        )
                    },
                    'komoditas':
                    (item, index)=>{
                        let komoditas
                        switch(item.kdKomoditas) {
                        case "01":
                            komoditas = "Beras"
                            break;
                        case "02":
                            komoditas = "Garam"
                            break;
                        case "03":
                            komoditas = "Hasil Perikanan"
                            break;
                        case "04":
                            komoditas = "Daging Lembu"
                            break; 
                        case "05":
                            komoditas = "Gula"
                            break;
                        }                                                                           
                        return(
                            <td>
                                {komoditas}
                            </td>
                        )
                    }
                }}
                />
            </div>
        </div>
        </>
    )
}

export default Viavalen